@use "./utils/colors" as colors;

@media screen and (min-width: 992px) {
  .container, .navbar {
    width: 960px;
  }

  .navbar {
    padding: 0;

    &__burger-icon {
      display: none;
    }

    &__links {
      display: block;
      flex-basis: auto;
    }

    &__list {
      flex-direction: row;
    }
  }

  .hero {
    &__text {
      padding-left: 1em;
    }

    &__description {
      font-size: 3.2rem;
    }
  }

  .about {
    &__description {
      flex-direction: row;

      & > * {
        flex-basis: 50%;
      }
    }

    &__title {
      text-align: initial;
    }

    &__text {
      font-size: 1.5rem;
    }

    &__banner-text {
      font-size: 3.2rem;
    }
  }

  .offer {
    &__card-group {
      flex-direction: row;
    }

    &__card {
      padding: 2em;

      &-option {
        font-size: 1.4rem;
      }
    }
  }

  .testimonials {
    &__slide {
      padding: 2em;
      border: 2px solid colors.$darkgray;

      &-logo {
        width: 200px;
      }
    }
  }

  .contact {
    &__box {
      flex-direction: row;
      align-items: flex-start;
    }

    &__details {
      flex-grow: 1;
    }

    &__form {
      flex-grow: 2;

      &-grid {
        grid-template-columns: repeat(2, 1fr);

        &-item {
          &:first-of-type,
          &:nth-last-of-type(2),
          &:last-of-type {
            grid-column: 1 / 3;
          }

          &:last-of-type {
            flex-direction: row;
          }
        }
      }

      &-error {
        font-size: 1.4rem;
      }
    }
  }

  .services {
    &__cards {
      flex-wrap: nowrap;
    }
  }

  .footer {
    &__nav {
      flex-direction: row;

      &-logo {
        flex-grow: 1;
      }
    }
  }

  .swiper-wrapper {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
}